.section{
  margin-bottom: 24px;
}

.secondary-title{
  display: flex;
  align-items: center;
  position: relative;
  height: 67px;
  padding-top: 8px;
  font-size: 16px;
}

.secondary-title .empty-circle,
.secondary-title .install-plugin svg{
  position: absolute;
  left: -36px;
}

.section > section {
  border: 1px solid var(--main-gray);  
}

.section > section:not(:first-of-type){
  border-top: none;
}

.section-header .main-title {
  margin: 0 60px; 
  font-size: 22px;  
  font-weight: 600;
  line-height: 65px;
}

.section-header{
  display: flex;
  align-items: center;
  height: 72px;
  background-color: var(--secondary-gray);  
}

.section:not(:last-of-type){
  margin-bottom: 24px;
}