.form-title{
  display: flex;
  justify-content: space-between;  
}

.form-title .paste-all .error-tooltip::after {
  border-top-color: var(--secondary-red);
}

.form-title > :last-child{
  margin: 0 10px;
}

.paste-all{
  position: relative;
  display: flex;
  color: var(--base-color);
  align-items: center;
  font-size: 12px;
  font-weight: 700;
  line-height: 16px;
}

.paste-all :first-child{
  margin: 0 10px;
}

.paste-all:hover{
  cursor: pointer;
}

.paste-all .cc-error.tooltip{
  position: absolute;
  white-space: nowrap;
  top: -65px;
  color: var(--red);
  background-color: var(--secondary-red);
  border-radius: 6px;
  padding: 10px 8px;  
}

.paste-all .cc-error.tooltip::after{
  position: absolute;
  content: '';
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 5px solid var(--secondary-red);
  bottom: -5px;
  left: 50%;
}