.cc-whitelist .cc-whitelist-details{
  font-size: 14px;
  font-weight: 300;
}

.cc-whitelist .cc-whitelist-button button{  
  margin-top: unset;
}

.cc-whitelist .cc-whitelist-button button:not(.success){    
  background: var(--base-light);
  color: var(--base-color);  
  border: unset;
}

.cc-whitelist-button{
  margin-top: 32px;
  padding-right: 48px;
  display: grid;
  grid-template-columns: 211px;
  grid-template-areas: "button error";
  gap: 10px;
}

.cc-whitelist__multi .cc-whitelist-button{
  padding-right: unset;
}

.cc-whitelist-button .error-wrapper{
  grid-area: error;
  font-size: 14px;
}

.cc-whitelist-button .cc-button {
  grid-area: button;
}

.cc-whitelist .cc-whitelist-button button.disable{
  background-color: var(--gray);
  color: white;
}

.cc-whitelist-button button.disable path{
  stroke: white;
}

.cc-whitelist .cc-whitelist-button .auth-button-text{
  display: flex;
  flex-wrap: nowrap;
  font-size: 12px;
  gap: 5px;
  padding:10px 10px;
}

.cc-whitelist .cc-trash-icon{
  margin: auto 16px;  
}

.cc-whitelist .cc-trash-icon:hover{
  cursor: pointer;
}

.cc-whitelist-buttons .cc-button.save{
  margin-top: 24px;
}