@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

:root {
  --base-color: #fc6723;
  --base-light: #FFF3F0;
  --main-gray: #d5d5d5;
  --secondary-gray: #F4F4F4;
  --red: #ff4747;
  --secondary-red: #FCDBDB;
  --green: #28b158;
  --error-color: #FF1300;
  --gray: #9B9B9B;  
}

#wp-cc-plugin {
  background-color: #ffffff;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: "poppins", sans-serif;
}
.border {
  border: 1px solid var(--main-gray);
}

.main-header {
  border: 1px solid var(--main-gray);
  margin-bottom: 24px;
}

.sub-header {
  display: flex;
  align-items: center;  
}


.logo {
  margin: 40px 0px 40px 52px;
}

.install-plugin {
  display: flex;
  align-items: center;
}

.content {
  width: 1016px;
  margin: 0 auto;
  margin-bottom: 41px;
}
.install-plugin .check-mark-circle {
  margin: 32px 0 36px 65px;
  vertical-align: middle;
}

.install-plugin-low .check-mark-circle {
  margin: 40px 0 43px 65px;
  vertical-align: middle;
}

.empty-circle {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid var(--main-gray);
}
.display-inline-block {
  display: inline-block;
}
.display-block {
  display: block;
}
.empty-circle-text {
  font-size: 16px;
}
.section-body {
  margin: 0 88px 32px 97px;
}
.key-input {
  width: 100%;
  height: 48px;
  border-radius: 7px;
  border: 1px solid var(--gray);  
  padding-left: 16px;
}
.input-icon {
  position: absolute;
  right: 16px;
  cursor: pointer;
}
.position-relative {
  position: relative;
}

.disable {
  background-color: var(--gray);
  pointer-events: none;
}
.enable {
  background-color: #fc6723;
}

.tracking-code-desc {
  margin: 9px 0 16px 0;
  font-size: 14px;
}

header {
  height: 142px;
}
.input-title {
  font-weight: 400;
  font-size: 14px;
}
input:focus::placeholder {
  color: transparent;
}
.copy-dialog {
  height: 23px;
  width: 55px;
  margin: 30px;
  background: var(--base-color);
  border-radius: 4px;
  color: white;
  position: absolute;
  top: 42px;
  right: -40px;
  z-index: 500;
}

.copy-dialog:after {
  content: " ";
  border-bottom: 10px solid var(--base-color);
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  position: relative;
  top: -26px;
  right: 36px;
}
.install-tracking-code {
  color: var(--base-color);
  cursor: pointer;
  text-decoration: underline;
  font-size: 14px;
  font-weight: 400;
  margin: 16px 0px 32px 97px;
}
.alert-remove {
  display: inline-block;
  float: right;
  margin-right: 88px;
  font-weight: 400;
  font-size: 14px;
}
.alert {
  color: var(--green);
}
.remove {
  color: var(--gray);
  cursor: pointer;
  text-decoration: underline;
  display: inline-block;
  margin-left: 10px;
}

.success-msg {
  color: var(--green);
}
. {
  font-weight: 400;
  font-size: 12px;
  margin-top: 10px;
}
.protected {
  font-size: 14px;
  font-weight: 400;
  margin: 16px 0px 32px 97px;
}
.copy-dialog-text {
  padding: 4px;
}
.auth-button-text {
  padding-left: 8px;
}
.cursor-not-allowed {
  cursor: not-allowed;
}

.input-wrapper{
  position: relative;  
  display: flex;
  width: 100%;
  align-items: center;
  margin-top: 8px;
  margin-bottom: 16px;
}

.input-wrapper > .cc-input{
  display: flex;
  flex-grow: 1;
  position:relative;
  align-items: center;
}

.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  opacity: 1;
  transition: opacity 200ms;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  transition: opacity 200ms;
}

.cc-flex-column{
  display: flex;
  flex-direction: column;
}

.cc-flex{
  display: flex;
}